.aniSpace {
  position: relative;
  margin-bottom: 50px;
}

.aniSpace img {
  object-fit: cover;
  box-shadow: 10px 10px 10px #100916e0;
}

.overlay {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  text-align: center;
  opacity: 0;
  transition: opacity .25s;
  background:  #0f0814;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1em;
  padding: 30px;
  box-shadow: 10px 10px 10px #100916e0;
}

.overlay h1 {
  margin-bottom: 0;
}

.aniSpace:hover .overlay {
  opacity: 1;
}
.aniList {
  width: calc(100% - 80px);
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
